import Component from '../react/component'

const ReactDom = {
    render
}

function render(vnode,container){
    return container.append(_render(vnode));
}
function _render(vnode){
    if(vnode == undefined || vnode ==='null' || typeof vnode == 'boolean') return;
    if(typeof vnode == 'string'){
        return document.createTextNode(vnode);
    }else if(typeof vnode.tag == 'function'){
        //1. åˆ›å»ºç»„ä»¶
        const comp = createComponent(vnode.tag,vnode.attrs);

        //2. è®¾ç½®ç»„ä»¶å±æ€§
        setComponentProps(comp,vnode.attrs);

        //3. ç»„ä»¶æ¸²æŸ“çš„èŠ‚ç‚¹å¯¹è±¡è¿”å›
        console.log(comp.base)
        return comp.base;
    }else if(typeof vnode == 'object'){
        let {tag,attrs,children} = vnode;
        const dom = document.createElement(tag);
        if(attrs){
            for(let key in attrs){
                const value = attrs[key];
                setAttribute(dom,key,value)
            }
        }
        children.forEach((child)=>{
            render(child,dom)
        })
        return dom;
    }

}
function setComponentProps(comp,props){
    //è®¾ç½®å±æ€§
    comp.props = props;
    // æ¸²æŸ“ç»„ä»¶
    renderComponent(comp)
}
function renderComponent(comp){
    const renderer = comp.render(); // è¿”å›çš„jsxå¯¹è±¡
    comp.base = _render(renderer)// å°†jsxè½¬åŒ–ä¸ºdom
}

function createComponent(comp,props){
    let inst;

    // å¦‚æœæ˜¯ç±»å®šä¹‰çš„ç»„ä»¶ åˆ™åˆ›å»ºå®ä¾‹ è¿”å›
    if(comp.prototype && comp.prototype.render){
        inst = new comp(props);
    }else{
        // å¦‚æœæ˜¯å‡½æ•°ç»„ä»¶ï¼Œå°†å‡½æ•°ç»„ä»¶è½¬åŒ–ä¸ºç±»ç»„ä»¶ æ–¹ä¾¿åé¢ç»Ÿä¸€ç®¡ç†å¤„ç†
        // compæ˜¯å‡½æ•°æœ¬èº« è¿”å›ä¸€ä¸ªjsxå¯¹è±¡
        inst = new Component(props);
        // ä¿®æ”¹thisæŒ‡å‘
        inst.constructor = comp;
        //å®šä¹‰renderå‡½æ•°
        inst.render = function(){
            return this.constructor(props);
        }
    }
    return inst;
}

function setAttribute(dom,key,value){
    if(key == 'className'){
        key = 'class';
    }

    //äº‹ä»¶onClick onBlur
    if(/on\w+/.test(key)){
        key = key.toLowerCase();
        dom[key] = value || ''
    }else if(key == 'style'){
        if(!value || typeof value =='string'){
            dom.style.cssText = value || '';
        }else if(value && typeof value == 'object'){
            for(let k in value){
                if(typeof value[key] == 'number'){
                    dom.style[k] = value + 'px';
                }else{
                    dom.style[k] = value;
                }
            }
        }
    }else{
        if(key in dom){
            dom[key] = value || ''
        }
        if(value){
            dom.setAttribute(key,value)
        }else{
            dom.removeAttribute(key);
        }
    }
}
export default ReactDom;